import $ from 'jquery';
import { TweenMax, TimelineLite, CSSPlugin }from 'gsap';
import _ from 'lodash';

$(() => {
  let $window = $(window);
  let images = document.getElementsByClassName('post_image');//$('.post_image');
  let $posts = $('.post');
  let titles = $('.post_title');
  let subtitles = $('.post_subtitle');
  let contents = $('.post_content');

  check_if_in_view();
  console.log(TimelineMax);
  function parallax(event){
    let x, y;
    y = Math.round(event.clientY / (window.innerHeight/100));
    y= (y - 50)*0.5;
    x = Math.round(event.clientX / (window.innerWidth/100));
    x= (x - 50)*0.5;
    TweenMax.to(images, .3, { css:{ y: y*0.8 + 'px',x: x*0.8 + 'px' }, ease: 'easeInOut' } );
    TweenMax.to(titles, .3, { css:{ y: y*0.5 + 'px',x: x*0.5 + 'px' }, ease: 'easeInOut' } );
    TweenMax.to(subtitles, .3, { css:{ y: y*0.5 + 'px',x: x*0.5 + 'px' }, ease: 'easeInOut' } );
    TweenMax.to(contents, .3, { css:{ y: y*0.5 + 'px',x: x*0.5 + 'px' }, ease: 'easeInOut' } );
  }
  
  $(document).mousemove(
		_.debounce(function(event){
      parallax(event)
    })
  );

  $(window).scroll(
		_.debounce(function(event){
      check_if_in_view()
    })
  );

  const openPost = () => {

  };

  //$('.post_image').mouseenter(
	//	_.debounce(function(event){
  //    TweenMax.to(event.target, 1, { css:{ scale: '1.08' }, ease: 'easeInOut' } );
  //  })
  //);
  //
  //$('.post_image').mouseout(
	//	_.debounce(function(event){
  //    TweenMax.to(event.target, .3, { css:{ scale: '1' }, ease: 'easeInOut' } );
  //  })
  //);

  $('.post_wrapper').click(function(e){
    e.preventDefault();
    let container = $(e.target).closest('.container');
    let otherContainer = container.siblings('.container');
    if (container.hasClass('right_container')){
      TweenMax.to(otherContainer, .4, { autoAlpha: 0 } );
      TweenMax.to(container, .4, { css:{ width: 100 + '%', left: 0 , 'z-index': 2} } );
    }else{
      TweenMax.to(otherContainer, .4, { autoAlpha: 0 } );
      TweenMax.to(container, .4, { css:{ width: 100 + '%' , 'z-index': 2} } );
    }
    console.log(container);
  })

  function check_if_in_view() {
    let window_height = $window.height();
    let window_top_position = $window.scrollTop();
    let window_bottom_position = (window_top_position + window_height);

    $.each($posts, function() {
      let $element = $(this);
      let element_height = $element.outerHeight();
      let element_top_position = $element.offset().top;
      let element_bottom_position = (element_top_position + element_height);

      //check to see if this current container is within viewport
      if ((element_bottom_position >= window_top_position) &&
          (element_top_position <= window_bottom_position)) {
        $element.addClass('in-view');
      } else {
        $element.removeClass('in-view');
      }
    });
  }
});
